import React, { useState } from 'react'
import { AiFillCloseCircle } from 'react-icons/ai';
import { actiontype } from '../../reducer/Reducer';
import { useStateValue } from '../../reducer/StateProvider';
import axios from "../../utils/API";
import openMessage, { Wrongmessage } from './openMessage';

const AddNewFolder = () => {



  const [, dispatch] = useStateValue();
  const [filename, setfilename] = useState("");

  const userid = localStorage.getItem("idmustakdem");

  const closerenameform = () => {
    dispatch({
      type: actiontype.SET_OVERMENUE,
      items: false,
    });

    dispatch({
      type: actiontype.SET_SHOWADDFOLDERFORM,
      items: false,
    });
  }

  const Addfolder = () => {
    //const userone = await axios.post(`users/${1}`);

    try {

      if (filename === "") {
        //  alert("Ÿäÿ±ÿ¨Ÿâ ÿßÿØÿÆÿßŸÑ ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ¨ŸÑÿØ")
        Wrongmessage("Ÿäÿ±ÿ¨Ÿâ ÿßÿØÿÆÿßŸÑ ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ¨ŸÑÿØ");
      }
      else {


        const idfold = Math.floor(Math.random() * 69566545);
        axios
          .post("folder", {
            iduser: userid,
            namefolder: filename,
            id: idfold,
            empollyadd: "aso",
            hystory: "",
          })
          .then();

        closerenameform();
        openMessage();
        //alert("ÿ™ŸÖ ÿßÿ∂ÿßŸÅÿ© ŸÖÿ¨ŸÑÿØ ÿ¨ÿØŸäÿØ");

      }


    } catch (error) {
      //console.log(error);
    }


  }



  return (
    <div className="rename">

      <div className="renameheader"><h4>ÿßÿ∂ÿßŸÅÿ© ŸÖÿ¨ŸÑÿØ ÿ¨ÿØŸäÿØ</h4> <AiFillCloseCircle onClick={closerenameform} size={30} /></div>

      <input placeholder="ÿßÿ≥ŸÖ ŸÖÿ¨ŸÑÿØ ÿßŸÑÿ≥ŸÜÿ© ÿßŸÑŸÖÿßŸÑŸäÿ© ŸÖÿ´ŸÑ ÿßŸÑÿ≥ŸÜÿ© ÿßŸÑŸÖÿßŸÑŸäÿ© 2021" className="inputrename" type="text" value={filename} onChange={(e) => setfilename(e.target.value)} />

      <div className="btnrename">
        <button onClick={Addfolder}>ÿßÿ∂ÿßŸÅÿ©</button> <button onClick={closerenameform}>ÿßŸÑÿ∫ÿßÿ°</button>
      </div>




    </div>
  )
}

export default AddNewFolder
