import React, { useEffect, useState } from "react";
import { AiOutlineUser } from "react-icons/ai";
import { BiLogOutCircle } from "react-icons/bi";
import { FaUserCircle } from "react-icons/fa";
import { MdAttachMoney, MdComment, MdFilter, MdHome, MdMessage, MdNotifications } from "react-icons/md";

import { BsChatSquareQuoteFill } from "react-icons/bs";



import { useHistory } from "react-router-dom";
import { actiontype } from "../../reducer/Reducer";
import { useStateValue } from "../../reducer/StateProvider";
import axios from "../../utils/API";
import { message } from "antd";

const SlideUsers = ({
  clas = "SlideUsershidde"
}) => {
  const history = useHistory();
  const [{ clickiconmenufolder_REDUCER }, dispatch] = useStateValue();
  const [managerusers, setmanagerusers] = useState(false);
  const [username, setusername] = useState("");
  const [userphone, setuserphone] = useState("");
  const [Bakfrom, setBakfrom] = useState("");

  const iduser = localStorage.getItem("idusee");
  const idmk = localStorage.getItem("idmustakdem");


  const userone = async (idus) => {
    const data = await axios.post(`users/${idus}`);
    setusername(data.data.username);
    setuserphone(data.data.phone);
    if (data.data.role === "ŸÖÿØŸäÿ± ŸÜÿ∏ÿßŸÖ") {
      setmanagerusers(true);
      setBakfrom("/FilesUsers");
    } else {
      setmanagerusers(false);
      setBakfrom("/Mustakdem");
    }
  };



  useEffect(() => {
    userone(iduser);

  }, [iduser]);


  useEffect(() => {
    const usermustakem = async (idmkl) => {
      const data = await axios.post(`users/${idmkl}`);
      setusername(data.data.username);

    }
    usermustakem(idmk);

  });

  const loguotuser = async () => {
    localStorage.removeItem("LOGINUSER");
    localStorage.removeItem("idusee");


    history.push("/login", { from: Bakfrom });

    dispatch({
      type: actiontype.SET_OVERMENUE,
      items: false,
    });
  };

  const gotoh = () => {
    history.push("/", { from: Bakfrom });
    dispatch({
      type: actiontype.SET_OVERMENUE,
      items: false,
    });
  }

  const gotoMaliaUser = () => {
    history.push("/MaliaUser", { from: Bakfrom });
    dispatch({
      type: actiontype.SET_OVERMENUE,
      items: false,
    });
  }



  const gotomanageuser = () => {
    history.push("/usersmange", { from: "/FilesUsers" });

    dispatch({
      type: actiontype.SET_OVERMENUE,
      items: false,
    });
  };

  const gotomyfiles = async () => {



    const userone = await axios.post(`users/${iduser}`);
    if (userone.data.role === "ŸÖÿØŸäÿ± ŸÜÿ∏ÿßŸÖ") {
      history.push("/FilesUsers", { from: "FilesUsers" });
    } else {
      history.push("/Mustakdem", { from: "FilesUsers" });
    }

    dispatch({
      type: actiontype.SET_OVERMENUE,
      items: false,
    });
  };


  return (
    <div className={clickiconmenufolder_REDUCER ? clas : "SlideUsershidde"}>
      <FaUserCircle size={40} />
      <h5>{username}</h5>
      <h5>{userphone}</h5>
      <h5>-----------------------</h5>

      {managerusers ? (
        <div
          className="slidbtn"
          style={{ cursor: "pointer" }}
          onClick={gotomanageuser}
        >
          <h5>ÿßÿØÿßÿ±ÿ© ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ</h5> <AiOutlineUser />
        </div>
      ) : (
        ""
      )}


      <div
        className="slidbtn"
        style={{ cursor: "pointer" }}
        onClick={gotomyfiles}
      >
        <h5>ÿßŸÑŸÖŸÑŸÅÿßÿ™</h5> <MdFilter />
      </div>


      <div
        className="slidbtn"
        style={{ cursor: "pointer" }}
        onClick={gotoMaliaUser}
      >
        <h5>ŸÇÿ≥ŸÖ ÿßŸÑŸÖÿßŸÑŸäÿ©</h5> <MdAttachMoney />
      </div>

      <div
        className="slidbtn"
        style={{ cursor: "pointer" }}
        onClick={() => message.info("ŸÇÿ±Ÿäÿ®ÿßŸã")}
      >
        <h5>ÿßŸÑÿ£ÿ¥ÿπÿßÿ±ÿßÿ™</h5> <MdNotifications />
      </div>

      <div
        className="slidbtn"
        style={{ cursor: "pointer" }}
        onClick={() => message.info("ŸÇÿ±Ÿäÿ®ÿßŸã")}
      >
        <h5>ÿßŸÑÿßÿπŸÑÿßŸÜÿßÿ™</h5> <MdMessage />
      </div>

      <div
        className="slidbtn"
        style={{ cursor: "pointer" }}
        onClick={() => message.info("ŸÇÿ±Ÿäÿ®ÿßŸã")}
      >
        <h5>ÿßŸÑÿ∑ŸÑÿ®ÿßÿ™</h5> <BsChatSquareQuoteFill />
      </div>


      <div
        className="slidbtn"
        style={{ cursor: "pointer" }}
        onClick={gotoh}
      >
        <h5>ÿßŸÑÿµŸÅÿ≠ÿ©ÿßŸÑÿ±ÿ¶Ÿäÿ≥Ÿäÿ©</h5> <MdHome />
      </div>
      <div
        className="slidbtn"
        style={{ cursor: "pointer" }}
        onClick={() => message.info("ŸÇÿ±Ÿäÿ®ÿßŸã")}
      >
        <h5>ÿ™ŸàÿßÿµŸÑ ŸÖÿπŸÜÿß</h5> <MdComment />
      </div>

      <div
        className="slidbtn"
        style={{ cursor: "pointer", background: "#d8be4a", marginTop: "auto" }}
        onClick={loguotuser}
      >
        <h5>ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿÆÿ±Ÿàÿ¨</h5> <BiLogOutCircle />
      </div>


    </div>
  );
};

export default SlideUsers;
