import React, { useEffect, useState } from "react";

import "./style.css";
import NavDashBoard from ".././NavDashBoard";
import { AiOutlineUserAdd, AiFillFolderAdd } from "react-icons/ai";
import { BiSearchAlt } from "react-icons/bi";
import { FcDeleteDatabase } from "react-icons/fc";
import { useHistory } from "react-router-dom";
import { AiOutlineMenuUnfold } from "react-icons/ai";

import axios from "../../utils/API";

import { useStateValue } from "../../reducer/StateProvider";
import { actiontype } from "../../reducer/Reducer";
import TRbody from "./TRbody";
import { message } from "antd";

const Users = () => {
        const userid = localStorage.getItem("idusee");
        const [datauser, setdatauser] = useState([{}]);
        const [serchnameuser, setserchnameuser] = useState("");
        const [showdivfolder, setshowdivfolder] = useState(false);
        const [addfolderall, setaddfolderall] = useState(true);
        const [deletallusers, setdeletallusers] = useState(true);
        const [adduser, setadduser] = useState(true);
        const [progess, setprogess] = useState(0);
        const history = useHistory();

        const getCurrentDate = (separator = "") => {
                let newDate = new Date();
                let date = newDate.getDate();
                let month = newDate.getMonth() + 1;
                let year = newDate.getFullYear();

                return `${year}${separator}${
      month < 10 ? `0${month}` : `${month}`
    }${separator}${date}`;
  };

  const susers = () => {
    if (serchnameuser === "") {
      axios.get("users").then((res) => setdatauser(res.data));
    } else {
      const getserchuser = datauser
        .filter((res) => res.username === serchnameuser)
        .map((re) => re);
      setdatauser(getserchuser);
    }
  };

  const [, dispatch] = useStateValue();
  const getusers = async () => {
    const user = await axios.get("users");
    setdatauser(user.data);
  };

  const [loadinimg, setloadinimg] = useState(false);

  useEffect(() => {
    if (progess === 100) {
      setTimeout(() => {
        message.success("ÿ™ŸÖ ÿßŸÑÿπŸÖŸÑŸäÿ© ÿ®ŸÜÿ¨ÿßÿ≠");
        setprogess(0);
        setloadinimg(false);
      }, 1000);
    }
  }, [progess]);

  const deleteallusers = async (event) => {
    event.persist();
    const config = {};
    const userone = await axios.post(`users/${userid}`);
    //console.log(userone.data.username)
    if (userone.data.permistoin_users[0].deletallusers === "ŸÑÿß") {
      message.error("ŸÑÿß ÿ™ŸÖÿ™ŸÑŸÉ  ÿµŸÑÿßÿ≠Ÿäÿ© ÿ≠ÿ∞ŸÅ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ ");
    } else {
      let answer = window.confirm(" ŸáŸÑ ÿßŸÜÿ™ ŸÖÿ™ÿßŸÉÿØ ŸÖŸÜ ÿ≠ÿ∞ŸÅ ÿ¨ŸÖŸäÿπ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜÿü");
      if (answer) {
        setloadinimg(true);
        datauser.map(async (res) =>
          res.role === "ŸÖÿ≥ÿ™ÿÆÿØŸÖ"
            ? await axios.post(
                `users//deluser/${res.iduser}`,
                {},
                {
                  onUploadProgress: (progressEvent) =>
                    //console.log(Math.round((progressEvent.loaded * 100) / progressEvent.total));
                    setprogess(
                      Math.round(
                        (progressEvent.loaded * 100) / progressEvent.total
                      )
                    ),
                }
              )
            : ""
        );

        const user = await axios.get("users", config);
        setdatauser(user.data);
        dispatch({
          type: actiontype.SET_DATAUESER,
          items: user.data,
        });
      } else {
      }
    }
  };

  const addnewuser = async () => {
    const userone = await axios.post(`users/${userid}`);
    if (userone.data.permistoin_users[0].adduser === "ŸÑÿß") {
      message.error("ŸÑÿß ÿ™ŸÖÿ™ŸÑŸÉ ÿµŸÑÿßÿ≠Ÿäÿ© ÿßÿ∂ÿßŸÅÿ© ŸÖÿ≥ÿ™ÿÆÿØŸÖ");
    } else {
      history.push("/addusers", { from: "Users" });
    }
  };

  const LOGINUSER = localStorage.getItem("LOGINUSER");

  try {
    useEffect(() => {
      axios.get("users").then((res) => setdatauser(res.data));

      const peruser = async (userid) => {
        if (LOGINUSER === null || userid === null) {
          history.push("/login", { from: "Users" });
        } else {
          const userone = await axios.post(`users/${userid}`);
          if (userone.data.permistoin_users[0].show === "ŸÑÿß") {
            setshowusers(false);
          }
          if (userone.data.permistoin_users[0].deletallusers === "ŸÑÿß") {
            setdeletallusers(false);
          }

          if (userone.data.permistoin_users[0].adduser === "ŸÑÿß") {
            setadduser(false);
          }

          if (userone.data.permistoin_users[0].addfolderall === "ŸÑÿß") {
            setaddfolderall(false);
          }
        }
      };

      peruser(userid);

      getusers();
    }, [userid, LOGINUSER, history]);
  } catch (error) {}

  const [showusers, setshowusers] = useState(true);
  const [namefolder, setnamefolder] = useState("");

  const addfolderalluser = async () => {
    const userone = await axios.post(`users/${userid}`);

    if (userone.data.permistoin_users[0].addfolderall === "ŸÑÿß") {
      message.error(" ŸÑÿß ÿ™ŸÖÿ™ŸÑŸÉ ÿµŸÑÿßÿ≠Ÿäÿ© ÿßÿ∂ÿßŸÅÿ© ŸÖÿ¨ŸÑÿØ ŸÑŸÉŸÑ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ");
    } else {
      setshowdivfolder(true);
    }
  };

  const canceladdfolder = () => {
    setshowdivfolder(false);
    setnamefolder("");
  };

  const addfoldernew = async () => {
    const userone = await axios.post(`users/${userid}`);
    //console.log(userone.data.username)
    if (userone.data.permistoin_users[0].addfolderall === "ŸÑÿß") {
      message.error("ŸÑÿß ÿ™ŸÖÿ™ŸÑŸÉ ÿµŸÑÿßÿ≠Ÿäÿ© ÿßÿ∂ÿßŸÅÿ© ÿßŸÑŸÖÿ¨ŸÑÿØ");
    } else {
      if (namefolder === "") {
        message.warning("Ÿäÿ±ÿ¨Ÿâ ÿßÿØÿÆÿßŸÑ ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ¨ŸÑÿØ");
      } else {
        setshowdivfolder(false);
        //message.success("ÿ™ŸÖ ÿßÿ∂ÿßŸÅÿ© ŸÖÿ¨ŸÑÿØ ŸÑÿ¨ŸÖŸäÿπ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ");
        const hystory = ` ${getCurrentDate("/")} ÿ®ÿ™ÿßÿ±ŸäÿÆ ${
          userone.data.username
        } ÿ™ŸÖ ÿßÿ∂ÿßŸÅÿ© ÿßŸÑŸÖÿ¨ŸÑÿØ ŸÖŸÜ ŸÇÿ®ŸÑ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ`;
        //const idfolder = Math.floor(Math.random() * 69566545);
        datauser.map(
          async (res) =>
            await axios.post(
              "folder",
              {
                iduser: res.iduser,
                namefolder,
                id: Math.floor(Math.random() * 69566545),
                empollyadd: userone.data.username,
                hystory,
              },
              {
                onUploadProgress: (progressEvent) =>
                  //console.log(Math.round((progressEvent.loaded * 100) / progressEvent.total));
                  setprogess(
                    Math.round(
                      (progressEvent.loaded * 100) / progressEvent.total
                    )
                  ),
              }
            )
        );
        setshowdivfolder(false);
        setnamefolder("");
      }
    }

    //getusers();
  };

  const [loadingpage, setloadingpage] = useState(true);
  const puser = async () => {
    const userone = await axios.post(`users/${userid}`);
    if (userone.data.role === "ŸÖÿ≥ÿ™ÿÆÿØŸÖ") {
      history.push("/Mustakdem", { from: "ProtectedRoute" });
      setloadingpage(true);
    } else {
      setloadingpage(false);
    }
  };

  puser();

  const [openmeunmobail, setopenmeunmobail] = useState(false);

  return (
    <div>
      {loadinimg && (
        <div>
          <div className="overflowimg"></div>
          <img
            src={window.location.origin + "/img/loding.gif"}
            className="img_loding"
            alt="logo"
          />
        </div>
      )}

      {loadingpage && (
        <div>
          <div className="overflowimg"></div>
          <img
            src={window.location.origin + "/img/loding.gif"}
            className="img_loding"
            alt="logo"
          />
        </div>
      )}

      <NavDashBoard />
      <div className="users">
        <div className="users_header">
          <div>
            <AiOutlineMenuUnfold
              className="m_mobail"
              size={30}
              onClick={() => setopenmeunmobail(!openmeunmobail)}
            />
          </div>

          <div className="t_mobail">ÿßÿØÿßÿ±ÿ© ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ</div>

          {!showdivfolder && (
            <div style={{ position: "relative" }}>
              <input
                type="text"
                placeholder="ÿßŸÑÿ®ÿ≠ÿ´ ÿπŸÜ ŸÖÿ≥ÿ™ÿÆÿØŸÖ"
                className="input_seache_users"
                value={serchnameuser}
                onChange={(e) => setserchnameuser(e.target.value)}
              />
              <BiSearchAlt onClick={susers} className="box_serach" />
            </div>
          )}
          {!showdivfolder && (
            <div
              onClick={addfolderalluser}
              className="add_users t_mobail"
              style={{
                background: "#FFC107",
                color: "black",
                display: addfolderall ? "" : "none",
              }}
            >
              <h5 style={{ margin: 0 }}>
                <AiFillFolderAdd size={25} />
                ÿßÿ∂ÿßŸÅÿ© ŸÖÿ¨ŸÑÿØ ŸÑŸÉŸÑ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ
              </h5>
            </div>
          )}
          {showdivfolder && (
            <div className="divaddfolder t_mobail">
              <label>ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ¨ŸÑÿØ</label>
              <input
                className="input_seache_users"
                onChange={(e) => setnamefolder(e.target.value)}
                value={namefolder}
              />
              <button className="btn btn-danger mr-3" onClick={addfoldernew}>
                ÿßÿ∂ÿßŸÅÿ©
              </button>

              <button
                className="btn btn-danger mr-3"
                style={{ background: "slategrey", borderColor: "slategrey" }}
                onClick={canceladdfolder}
              >
                ÿßŸÑÿ∫ÿßÿ°
              </button>
            </div>
          )}
          {!showdivfolder && (
            <div
              onClick={deleteallusers}
              style={{ display: deletallusers ? "" : "none" }}
              className="t_mobail delall_users"
            >
              <h5 style={{ margin: 0, color: "#fff" }}>
                <FcDeleteDatabase size={25} /> ÿ≠ÿ∞ŸÅ ÿ¨ŸÖŸäÿπ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ
              </h5>
            </div>
          )}
          {!showdivfolder && (
            <div
              onClick={addnewuser}
              style={{ display: adduser ? "" : "none" }}
              className="t_mobail add_users"
            >
              {" "}
              <h5 style={{ margin: 0, color: "#fff" }}>
                ÿßÿ∂ÿßŸÅÿ© ŸÖÿ≥ÿ™ÿÆÿØŸÖ ÿ¨ÿØŸäÿØ <AiOutlineUserAdd size={25} />
              </h5>
            </div>
          )}
        </div>

        {openmeunmobail && (
          <div className="mune_mobail">
            {!showdivfolder && (
              <div
                onClick={addfolderalluser}
                className="add_users "
                style={{
                  background: "#FFC107",
                  color: "black",
                  display: addfolderall ? "" : "none",
                }}
              >
                <h5 style={{ margin: 0 }}>
                  <AiFillFolderAdd size={25} />
                  ÿßÿ∂ÿßŸÅÿ© ŸÖÿ¨ŸÑÿØ ŸÑŸÉŸÑ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ{" "}
                </h5>
              </div>
            )}

            {showdivfolder && (
              <div className="divaddfolder ">
                <label>ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ¨ŸÑÿØ</label>
                <input
                  className="input_seache_users"
                  onChange={(e) => setnamefolder(e.target.value)}
                  value={namefolder}
                />
                <button className="btn btn-danger m-3" onClick={addfoldernew}>
                  ÿßÿ∂ÿßŸÅÿ©
                </button>

                <button
                  className="btn btn-danger m-3"
                  style={{ background: "slategrey", borderColor: "slategrey" }}
                  onClick={canceladdfolder}
                >
                  ÿßŸÑÿ∫ÿßÿ°
                </button>
              </div>
            )}

            {!showdivfolder && (
              <div
                onClick={deleteallusers}
                style={{ display: deletallusers ? "" : "none" }}
                className="mt-3 delall_users"
              >
                <h5 style={{ margin: 0, color: "#fff" }}>
                  <FcDeleteDatabase size={25} /> ÿ≠ÿ∞ŸÅ ÿ¨ŸÖŸäÿπ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ
                </h5>
              </div>
            )}
            {!showdivfolder && (
              <div
                onClick={addnewuser}
                style={{ display: adduser ? "" : "none" }}
                className="mt-3 add_users"
              >
                {" "}
                <h5 style={{ margin: 0, color: "#fff" }}>
                  <AiOutlineUserAdd size={25} /> ÿßÿ∂ÿßŸÅÿ© ŸÖÿ≥ÿ™ÿÆÿØŸÖ ÿ¨ÿØŸäÿØ
                </h5>
              </div>
            )}
          </div>
        )}

        <div className="users_content">
          <table className="table table-striped">
            <thead className="thead-dark text-center">
              <tr>
                <th scope="col">#</th>
                <th scope="col">ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ</th>
                <th className="t_mobail" scope="col">
                  ÿ±ŸÇŸÖ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ
                </th>
                <th className="t_mobail" scope="col">
                  ÿ±ŸÇŸÖ ÿßŸÑŸáÿßÿ™ŸÅ
                </th>
                <th className="t_mobail" scope="col">
                  ÿßŸÑÿ≠ÿßŸÑÿ©
                </th>
                <th scope="col">ÿßŸÑŸÖŸÑŸÅÿßÿ™</th>

                <th scope="col" style={{ display: !showusers ? "none" : "" }}>
                  ÿ™ÿπÿØŸäŸÑ
                </th>
              </tr>
            </thead>
            <tbody className="text-center">
              {datauser.map((res, i) => (
                <TRbody key={i} {...res} i={i} pershowusers={showusers} />
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default Users;