import React ,{useState,useEffect} from 'react'
import { Result, Button } from 'antd';
import { useHistory } from 'react-router-dom';
import Main from "../../Pages/Main";
import { BrowserRouter as Router, Switch, Route ,Redirect} from "react-router-dom";


const NoInternet = () => {
const history = useHistory();

  const [isOnline, setNetwork] = useState(window.navigator.onLine);

useEffect(() => {
window.addEventListener("offline", 
        () => setNetwork(window.navigator.onLine)
      );
window.addEventListener("online", 
        () => setNetwork(window.navigator.onLine)
      );
},[isOnline]);


   const   GoToHome = () => {
    history.push("/", { from: "NoInternet" });
     history.replace("/login");
     window.location.reload();
   }
    return (
       <div>
      {isOnline?
        
<Router>
     
    
        <Switch>
        <Redirect  push   to={{
    pathname: "/login",
   
    state: { referrer: "/500" }
  }} />
    
     
    </Switch>
      </Router>
      :
      <Result
    status="500"
    title="500"
    subTitle="Ÿäÿ®ÿØŸà ÿßŸÜÿ© ŸÑÿß ŸäŸàÿ¨ÿØ ÿßŸÜÿ™ÿ±ŸÜŸäÿ™ ŸÅŸä ÿ¨Ÿáÿßÿ≤ŸÉ Ÿäÿ±ÿ¨Ÿâ ÿßŸÑÿ™ÿ£ŸÉÿØ ŸÖŸÜ ÿßÿ™ÿµÿßŸÑ ÿßŸÑÿßŸÜÿ™ÿ±ŸÜŸäÿ™ Ÿàÿ™ÿ≠ÿØŸäÿ´ ÿßŸÑÿµŸÅÿ≠ÿ©"
    extra={<Button type="primary" onClick={GoToHome}>ÿßŸÑÿ∞Ÿáÿßÿ® ÿßŸÑŸâ ÿµŸÅÿ≠ÿ© ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿØÿÆŸàŸÑ</Button>}
  />


      }
       </div>
    )
}

export default NoInternet
