import React, { useEffect, useState } from "react";
import "./style.css";
import * as Fi from "react-icons/fi";
import axios from "../../utils/API";
import { useHistory } from "react-router-dom";
import { message } from "antd";

const Login = () => {
  const [iduser, setiduser] = useState("");
  const [passuser, setpassuser] = useState("");
  const [loding, setloding] = useState(false);
  const userid = localStorage.getItem("idusee");
  const LOGINUSER = localStorage.getItem("LOGINUSER");
  const history = useHistory();

  useEffect(() => {
    try {
      const getperuser = async (userid) => {
        const userone = await axios.post(`users/${userid}`);
        if (LOGINUSER === null) {
          // setisAuthenticated(false)
          history.push("/login", { from: "ProtectedRoute" });
        } else {
          if (userone.data.role === "ŸÖÿØŸäÿ± ŸÜÿ∏ÿßŸÖ") {
            history.push("/usersmange", { from: "Login" });
          } else {
            history.push("/Mustakdem", { from: "Login" });
          }
        }

        if (userid === null) {
          // setisAuthenticated(false)
          history.push("/login", { from: "ProtectedRoute" });
        }
      };

      getperuser(userid);
    } catch (error) {
      console.log("");
    }
  }, [userid, LOGINUSER, history]);

  const gotohome = () => {
    history.push("/", { from: "Login" });
  };

  const loginuser = async () => {
    if (userid === "") {
      message.warn("ÿ≠ŸÇŸÑ ÿ±ŸÇŸÖ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ ŸÅÿßÿ±ÿ∫");
    } else if (passuser === "") {
      message.warn("ÿ≠ŸÇŸÑ ŸÉŸÑŸÖÿ© ÿßŸÑŸÖÿ±Ÿàÿ± ŸÅÿßÿ±ÿ∫");
    } else {
      const loginuser = await axios.post(`users/login/${iduser}&${passuser}`);
      // console.log(loginuser)
      if (loginuser.status === 201) {
        message.warn(loginuser.data.message);
      } else {
        const userone = await axios.post(`users/${loginuser.data.iduser}`);

        if (loginuser) {
          setloding(true);
          setTimeout(() => {
            localStorage.setItem("idusee", loginuser.data.iduser);
            localStorage.setItem("LOGINUSER", true);
            localStorage.setItem("idmustakdem", loginuser.data.iduser);

            if (userone.data.role === "ŸÖÿØŸäÿ± ŸÜÿ∏ÿßŸÖ") {
              history.push("/usersmange", { from: "Login" });
            } else {
              history.push("/Mustakdem", { from: "Login" });
            }

            setloding(false);
          }, 500);
        }
        //console.log(loginuser.data.iduser)
      }
    }
  };

  return (
    <div className="logn">
      {loding && (
        <div>
          <div className="overflowimg"></div>
          <img
            src={window.location.origin + "/img/loding.gif"}
            className="img_loding"
            alt="logo"
          />{" "}
        </div>
      )}

      <div className="login_overflow"></div>
      <div className="containe" style={{ zIndex: 58555 }}>
        <div
          className=""
          alt="logo"
          style={{ textAlign: "center", margin: 15 }}
        >
          <h3 style={{ fontWeight: 700 }}>
            {" "}
            ÿßŸÑŸÖÿ≠ÿßÿ≥ÿ® ÿßŸÑŸÇÿßŸÜŸàŸÜŸä ŸàÿßŸÑŸÖÿ≥ÿ™ÿ¥ÿßÿ± ÿßŸÑŸÖÿßŸÑŸä
          </h3>
          <p
            style={{
              fontFamily: "'Aref Ruqaa', serif",
              fontSize: 40,
              color: "rgb(27 32 84)",
              marginBottom: 0,
            }}
          >
            ÿ≥ÿπÿØŸä ŸÖÿ≠ÿ®ŸàŸèÿ®ÿ©
          </p>
        </div>
        <div className="contaner_input_login">
          <div className="container_div">
            <label>ÿ±ŸÇŸÖ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ</label>
            <input
              value={iduser}
              onChange={(e) => setiduser(e.target.value)}
              className="input_login"
              type="number"
              required
            />
          </div>
          <div className="container_div">
            <label>ŸÉŸÑŸÖÿ© ÿßŸÑŸÖÿ±Ÿàÿ±</label>

            <input
              value={passuser}
              onChange={(e) => setpassuser(e.target.value)}
              className="input_login"
              type="Password"
            />
          </div>

          <div className="container_div " style={{ marginTop: 30 }}>
            <button onClick={loginuser} className="input_login btn_login">
              ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿØÿÆŸàŸÑ <Fi.FiLogIn size={25} style={{ marginRight: 10 }} />
            </button>

            <button
              onClick={gotohome}
              style={{ background: "rgb(88 130 34)" }}
              className="input_login btn_login mt-3"
            >
              ÿßŸÑÿµŸÅÿ≠ÿ© ÿßŸÑÿ±ÿ¶Ÿäÿ≥Ÿäÿ©
              <Fi.FiHome size={25} style={{ marginRight: 10 }} />
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
